body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background-image: linear-gradient(to right, #14ebfa, #282be7);
}
nav {
  height: 20vh;
  width: 100%;
  color: black;
}
.navitems {
  display:block;
  text-align: center;
}
.navitems h2 {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 50px;
  -webkit-text-decoration: dashed;
          text-decoration: dashed;
  color: rgb(0, 0, 0);
  text-align: center;
}
.navitems button {
  width: 20%;
  height: 15vh;
  border-radius: 25px;
  background-color: rgb(27, 216, 43);
  font-family: cursive;
  font-size: 25px;
  margin: auto 0px;
}
.fetchbtn:hover {
  background-color:#48dee9;
}
.userdatacontainer {
  width: 100%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  background-image: black
}
#main {
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  overflow: hidden;
}
.name:hover {
  color:rgb(228, 13, 217);
}
.email:hover{
  color: rgb(235, 31, 31);
}
.avatar {
  border-radius: 20%;
}
.profile_cont {
  border-radius: 20%;
  background-image: radial-gradient(rgb(129, 253, 98), #eb7b7b);
  margin: 5px;
  padding: 10px;
  width: 40%;
  text-align: center;
  transition: -webkit-transform ease 0.2s;
  transition: transform ease 0.2s;
  transition: transform ease 0.2s, -webkit-transform ease 0.2s;
  opacity: 0.9;
  margin-top: 8vh;
  margin-bottom: 8vh ;
}
.profile_cont:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  opacity: 1;
}
.footer {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  font-size: large;
  color: #265824;
  font-weight: 400;
  font-family: serif;
  text-decoration: underline;
}
.button {
  font-size: large;
  font-style: italic;
}
